export default{
  getState: state => payload => state[payload],
  isVisible: state => payload => state[payload],
  getUser: state  => payload => state.user[payload],
  getObjState: state => payload => state[payload.obj][payload.prop],
  hideCloseBtn: state => (state.modal.current === 'research' || state.modal.current === 'scores'|| state.modal.current === 'login') ? true : false,  


  getScore: state => {
    const temp = []
    state.user.days.forEach(day =>{
      const dates = day.date.split(/[\s,]+/)
      day.scores.forEach(score =>temp.push({day: dates[0], month:dates[1], score}))
    })

    return temp
  }
}
