import sound from '@/views/partials/math-crush/sound'
import axiosInstance from '@/libs/axiosInstance'

export default{
  state: {
    grid: [],
    isVisible:{
      scoreCard: false
    },
    playing:false,
    maxLvl: 10,                   //👈 maximum  number of levels game currently has
    gridSize: 8,                 //👈 size of the game grid currently 8X8 = 64 game tiles
    pause: true,                 //👈 is game currently in pause mode
    currentLvL: 1,               //👈 current level
    currentScore: 0,             //👈 current Score
    playTime: 0,                 //👈 the total amount of minutes the user has played the game
    points: 10,                   //👈 the total amount of points awarded for each correct answer
    combo: 0,
    firstMove: null,
    clock:{
      add:10,
      minus:1,
      remaining:100
    },
    levels: [
      {
        goal: 50,               //👈 the number of points the user has to obtained in order to successfully complete a level
        target: 75,              //👈 the denomination the user has to make change for eg. lvl 1 amount = 90 ---- the user has to make change for $90,
        hints: [                 //👈 possible combinations of different denominations the users can use to achieve the target
          ['fifty','twenty','five'],
          ['fifty','ten','ten','five'],
          ['fifty','ten','five','five','five'],['twenty','twenty','twenty','ten','five'],
        ]
      },
      {
        goal: 100,                //👈 the number of points the user has to obtained in order to successfully complete a level
        target: 80,              //👈 the denomination the user has to make change for eg. lvl 1 amount = 90 ---- the user has to make change for $90,
        hints: [                 //👈 possible combinations of different denominations the users can use to achieve the target
        ['fifty','twenty','ten'],
        ['fifty','ten','ten','ten'],['fifty','twenty','five','five'],
        ['twenty','twenty','twenty','ten','ten'],['twenty','twenty','twenty','ten','ten'],['fifty','ten','ten','five','five']
      ]
      },
      {
        goal: 150,                //👈 the number of points the user has to obtained in order to successfully complete a level
        target: 90,              //👈 the denomination the user has to make change for eg. lvl 1 amount = 90 ---- the user has to make change for $90,
        hints: [                 //👈 possible combinations of different denominations the users can use to achieve the target
          ['fifty','twenty','twenty'],
          ['fifty','twenty','ten','ten'],
          ['fifty','twenty','ten','five','five'],['twenty','twenty','twenty','twenty','ten'],['fifty','ten','ten','ten','ten']
        ]
      },
      {
        goal: 200,                //👈 the number of points the user has to obtained in order to successfully complete a level
        target: 150,             //👈 the denomination the user has to make change for eg. lvl 1 amount = 90 ---- the user has to make change for $90,
        hints: [                 //👈 possible combinations of different denominations the users can use to achieve the target
          ['one-hundred','one-hundred','fifty'],
          ['one-hundred','fifty','fifty','fifty'],
          ['one-hundred','one-hundred','twenty','twenty','ten'],['fifty','fifty','fifty','fifty','fifty']
        ]
      },
      {
        goal: 300,                //👈 the number of points the user has to obtained in order to successfully complete a level
        target: 170,             //👈 the denomination the user has to make change for eg. lvl 1 amount = 90 ---- the user has to make change for $90,
        hints: [                 //👈 possible combinations of different denominations the users can use to achieve the target       
          ['one-hundred','fifty','twenty'],
          ['fifty','fifty','fifty','twenty'],['one-hundred','fifty','ten','ten'],
          ['fifty','fifty','fifty','ten','ten'],['twenty','twenty','twenty','ten','one-hundred'],['one-hundred','twenty','twenty','twenty','ten']
        ]
      },
      {
        goal: 350,                //👈 the number of points the user has to obtained in order to successfully complete a level
        target: 220,             //👈 the denomination the user has to make change for eg. lvl 1 amount = 90 ---- the user has to make change for $90,
        hints: [                 //👈 possible combinations of different denominations the users can use to achieve the target 
          ['one-hundred','one-hundred','twenty'],
          ['one-hundred','fifty','fifty','twenty'],['one-hundred','one-hundred','ten','ten'],
          ['fifty','fifty','fifty','fifty','twenty'],['one-hundred','fifty','fifty','ten','ten'],['one-hundred','fifty','fifty','ten','ten']
        ]
      },
      {
        goal: 400,                //👈 the number of points the user has to obtained in order to successfully complete a level
        target: 250,             //👈 the denomination the user has to make change for eg. lvl 1 amount = 90 ---- the user has to make change for $90,
        hints: [                 //👈 possible combinations of different denominations the users can use to achieve the target 
          ['one-hundred','one-hundred','fifty'],
          ['one-hundred','fifty','fifty','fifty'],
          ['fifty','fifty','fifty','fifty','fifty'],['one-hundred','one-hundred','twenty','twenty','ten']
        ]
      }, 
      {
        goal: 500,                //👈 the number of points the user has to obtained in order to successfully complete a level
        target: 650,             //👈 the denomination the user has to make change for eg. lvl 1 amount = 90 ---- the user has to make change for $90,
        hints: [                 //👈 possible combinations of different denominations the users can use to achieve the target 
          ['five-hundred','one-hundred','fifty'],
          ['five-hundred','fifty','fifty','fifty'],
          ['five-hundred','one-hundred','twenty','twenty','ten'],
        ]
      },
      {
        goal: 550,               //👈 the number of points the user has to obtained in order to successfully complete a level
        target: 700,             //👈 the denomination the user has to make change for eg. lvl 1 amount = 90 ---- the user has to make change for $90,
        hints: [                 //👈 possible combinations of different denominations the users can use to achieve the target 
          ['five-hundred','one-hundred','one-hundred'],
          ['five-hundred','one-hundred','fifty','fifty'],
          ['five-hundred','fifty','fifty','fifty','fifty']
        ]
      },
      {
        goal: 600,               //👈 the number of points the user has to obtained in order to successfully complete a level
        target: 1200,             //👈 the denomination the user has to make change for eg. lvl 1 amount = 90 ---- the user has to make change for $90,
        hints: ['NO HINTS THIS ROUND']    
      }
    ]
  },

  getters: {
    getPauseStatus: state => state.pause,
    getPlayingStatus: state => state.playing,
    getCrushState: state => prop => state[prop],
    getCrushCurrentLvL: state => state.currentLvL,
    getCurrentScore: state => state.currentScore,
    getHints: state => payload => state.levels[payload],
    getCrushLvlState: state => index => state.levels[index],
    getCrushObjState: state => payload => state[payload.obj][payload.prop],

    getCurrency: state => {
      const currencyOne = [{amount : 5,css: 'five'},{amount : 10,css: 'ten'},{amount : 50,css: 'fifty'},{amount : 20,css: 'twenty'},{amount : 100,css: 'one-hundred'},{amount : 500,css: 'five-hundred'}]
      const currencyTwo  = [{amount : 10,css: 'ten'},{amount : 50,css: 'fifty'},{amount : 20,css: 'twenty'},{amount : 100,css: 'one-hundred'},{amount : 500,css: 'five-hundred'},{amount : 500,css: 'five-hundred'}]
      return(state.currentLvL > 7) ? currencyTwo : currencyOne 
    }
  },

  mutations: {
    pauseGame(state){
      state.pause = !state.pause
    },

    startGame(state, payload){
      state.playing = payload
    },

    setGrid(state, gameTile){
      state.grid.push(gameTile)
    },

    increasePlayTime(state){
      state.playTime += 1500
    },
    increasePoints(state){
      if(state.firstMove)state.currentScore += state.points * state.combo
      else state.currentScore = 10
          
      state.combo = 0
    },
    increaseCombo(state){
        state.combo +=1
    },
    increaseLvL(state){
      state.currentLvL += 1
    },
    madeFirstMove(state, payload){
      state.firstMove = payload
    },
  
    toggleScoreCard(state){
      state.isVisible.scoreCard = ~state.isVisible.scoreCard 
    },

    increaseGameTime(state){ 
      const target =  document.getElementById('progress-bar-container').firstElementChild
      let time = parseInt(target.style.width)
      time += state.clock.add * state.combo
      
      if(time > 100)time = 100
      target.style.width = `${time}%`
    },

    newGame(state){
      const target= document.getElementById('progress-bar-container').firstElementChild
      document.getElementById('pauseBtn').innerText = 'pause'
      target.style.width = `100%`
      state.currentLvL +=1
      state.currentScore = 0
      state.firstMove = false
    },
    
    resetGrid(state){
      while (state.grid.length > 0) {state.grid.pop()}
      state.pause = true
      state.currentLvL = 1
      state.currentScore = 0
      state.firstMove = false
      state.grid.splice(0, state.grid.length)
    }
    
  },
  
  actions: {
    
  }//actions ends here
}//export ends here