import router from '@/router/index'

export default{
  disableScroll(state){
    document.querySelector("body").classList.add('overflow-hidden')
  },
  
  enableScroll(state){
    document.querySelector("body").classList.remove('overflow-hidden')
  },
  resetModal (state) {
    state.modal.current = null
    state.modal.isActive = null
  },

  setCurrentModal(state, current){
    state.modal.current = current
  },


  toggleModal(state, modal){
    state.modal.isActive = !state.modal.isActive
    state.modal.current= modal
  },

  toggleSideNav (state, collapse) {
    if(collapse || window.innerWidth < 768){
      state.sideNav = !state.sideNav
      const body = document.querySelector("body")
      body.classList.toggle('overflow-hidden')
    }
  }, 

  async redirect(state, payload){
    setTimeout(_ => router.push({name:payload.route}), payload.timeOut)
  },

  setResponse(state, res){
    state[res.state] = res.data
  },

  updateUser(state, payload){
    state.user[payload.prop] = payload.value
  },

  pushCurrentScore(state){
    let found = null
    const today = `${state.user.today.date} ${state.user.today.month}, ${state.user.today.year}`
    
    state.user.days.forEach(index => {
      if(index.date == today){
        index.scores.push(state.crush.currentScore)
        found = true
      }
    })
    if(!found)state.user.days.push({date: today,  scores: [state.crush.currentScore]})
  }
}//export ends here
