import store from '@/store/index'
import router from '@/router/index'

const removeQueryParams = to => {
  if(Object.keys(to.query).length)
    return {path: to.path, query: {}, hash: to.hash}
}


const isAdmin = async(to, from) => {
  const res = await store.dispatch('accountType')
  if(!res) router.push('/unauthorize-access')
  return res
}


const routes = [
  {
    path: '/', 
    name: 'home', 
    meta: { requiresAuth: true }, 
    component: () => import('@/views/partials/main-layout.vue'),
    children: 
    [
      { 
        path: '/', 
        name: 'dashboard', 
        meta: { requiresAuth: true }, 
        component: () => import('@/views/partials/dashboard.vue'),  
      },
      {
        path: '/information',
        name: 'information',
        meta: { requiresAuth: true }, 
        component: () => import('@/views/partials/dyscalculia.vue'),
        beforeEnter: [removeQueryParams]   
      },
      {
        path: '/statistics',
        name: 'statistics',
        meta: { requiresAuth: true, admin: true }, 
        component: () => import('@/views/partials/statistics.vue'),
        beforeEnter: [removeQueryParams, isAdmin]   
      },
      { 
        path: '/math-crush',
        name: 'math-crush',
        meta: { requiresAuth: true }, 
        component: () => import('@/views/partials/math-crush/main-layout.vue') 
      }
    ]
  },
  {
    path: '/internal-error',
    name: 'internal-error',
    meta: { requiresAuth: false }, 
    component: () => import('@/views/errors/internal-server.vue'),
  },
  {
    path: '/unauthorize-access',
    name: 'unauthorize-access',
    meta: { requiresAuth: false }, 
    component: () => import('@/views/errors/unauthorize-access.vue'),
  },
  {
    path: '/server-maintenance',
    name: 'server-maintenance',
    meta: { requiresAuth: false }, 
    component: () => import('@/views/errors/server-maintenance.vue'),
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'not-found',
    meta: { requiresAuth: false }, 
    component: () => import('@/views/errors/not-found.vue'),
  },
  { 
    path: '/', 
    name: 'welcome', 
    meta: { requiresAuth: false }, 
    component: () => import('@/views/partials/landing.vue'),
    beforeEnter: [removeQueryParams],
  }
]



export { routes}