import axiosInstance from "@/libs/axiosInstance"


export default{
  async fetchGame({commit}) {
    try {
      const res = await axiosInstance.get('game/all')
      commit('setResponse', {data: res.data, state: 'games'})
    } catch (err) {
        commit('redirect', {route: 'internal-error', timeOut: 0})   //👈 change this to reflect  results empty
    }
  },

  async fetchUserData({commit}) {
    try {
      const res = await axiosInstance.get('auth/login-success')
      commit('setResponse', {data: res.data, state: 'user'})
    } catch (err) {
        commit('redirect', {route: 'internal-error', timeOut: 0})   //👈 change this to reflect  results empty
    }
  },


  async fetchUserScores({commit}) {
    try {
      const res = await axiosInstance.get('game/users')
      return res.data
    } catch (err) {
        commit('redirect', {route: 'internal-error', timeOut: 0})   //👈 change this to reflect  results empty
    }
  },


  async saveScore({commit, state}){
    try {     
      const data = {
        highScore: state.user.highScore,
        minutes: state.crush.playTime,
        days: state.user.days,
      }
      const res = await axiosInstance.put('game/save', data)
    } catch (error) {
      console.log('saved failed');
    }
  },

}//export ends here
