import { createRouter, createWebHistory } from 'vue-router'
import{routes}  from '@/router/routes'
import store from '@/store/index'


const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})



/**
 * @summary before each route make a request to the server and retrieve login Status
 *          if user is Authenticated proceed as normal to the desired route
 *          else redirect to the landing page
 */
router.beforeEach(async(to, from, next) => {
  try {
    const status = await store.dispatch('loginStatus')      //store.state.loginStatus
    let needAuth = to.matched.some(item => item.meta.requiresAuth)
    if(needAuth && !status) return next({name:'welcome'})
    next()


  } catch (err) {console.log('error retrieving user status')}
})

export default router
